import React, { Component } from "react";
import Typography from "@material-ui/core/Typography";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import Button from "@material-ui/core/Button";
import "../../styles/preview.css";
import Webcam from "react-webcam";
import CheckIcon from "@material-ui/icons/Check";
import ClearIcon from "@material-ui/icons/Clear";
import InfoIcon from "@material-ui/icons/Info";
import birballogo from "../../assets/images/instructions/birballogo.png";
import AudioAnalyser from "./AudioAnalyser";
import Modal from "../../components/Atoms/Modal";
import * as Actions from "../../store/User/UserAction";
import { bindActionCreators } from "redux";
import { store } from "react-notifications-component";
import audio from "../../assets/images/audio.png";
import CircularProgress from "@material-ui/core/CircularProgress";
import Dialog from "@material-ui/core/Dialog";
import warning from "../../assets/images/warning.svg";
import ArrowRightIcon from "@material-ui/icons/ArrowRight";
import ReactGA from "react-ga";
import ScreenRecording from "../Test/screenRecording";
import MicIcon from "@material-ui/icons/Mic";

let indexedDB =
    window.indexedDB ||
    window.webkitIndexedDB ||
    window.mozIndexedDB ||
    window.OIndexedDB ||
    window.msIndexedDB,
  IDBTransaction =
    window.IDBTransaction ||
    window.webkitIDBTransaction ||
    window.OIDBTransaction ||
    window.msIDBTransaction,
  dbVersion = 2;

let request = indexedDB.open("elephantFiles", dbVersion);

request.onsuccess = function (event) {
  let db = event.target.result;

  db.onerror = function (event) {
    console.log("Error creating/accessing IndexedDB database");
  };
};
request.onupgradeneeded = function (event) {
  let db = event.target.result;
  let objectStore = db.createObjectStore("videoAnswers", {
    keyPath: "questionId",
  });
};

let amplitude = 0;

export class Preview extends Component {
  constructor(props) {
    super(props);

    this.state = {
      alert: false,
      enter: false,
      audio: false,
      video: true,
      audioAccess: false,
      videoAccess: false,
      screenAccess: false,
      audioNew: null,
      isShowModal: false,
      screenShot: null,
      faces: 0,
      speedInMbps: 0,
      showDialog: false,
      source: "",
      responseId: "",
      showDialogScreen: false,
      audioPopup: false,
      voiceMessage: "",
    };

    this.videoTag = React.createRef();
    this.webcamRef = React.createRef();
    this.childRef = React.createRef();
  }

  componentDidMount = async () => {
    const { response } =
      this.props &&
      this.props.history &&
      this.props.history.location &&
      this.props.history.location.state;
    await this.setState({
      responseId: response.id,
    });
    ReactGA.pageview(window.location.pathname);
    window.onbeforeunload = function () {
      return true;
    };

    await navigator.mediaDevices
      .getUserMedia({ audio: true })
      .then(async (stream) => {
        await this.setState({ audioAccess: true });
      })
      .catch((e) => {
        this.setState({ audioAccess: false, isShowModal: true });
        this.props.UserAction.addEvent({
          responseId: this.state.responseId,
          event: {
            type: "interview",
            event: "Preview check failed",
            message: "Audio Access not granted",
            timestamp: new Date().toISOString(),
          },
          isPreviewFail: true,
        });
      });

    await navigator.mediaDevices
      .getUserMedia({ video: this.state.video })
      .then((stream) => {
        this.setState({ videoAccess: true });
        // this.capture();
      })
      .catch(() => {
        this.setState({ videoAccess: false, isShowModal: true });
        this.props.UserAction.addEvent({
          responseId: this.state.responseId,
          event: {
            type: "interview",
            event: "Preview check failed",
            message: "Camera Access not granted",
            timestamp: new Date().toISOString(),
          },
          isPreviewFail: true,
        });
      });

    if (!this.state.videoAccess && !this.state.audioAccess) {
      this.setState({
        source:
          "https://birbal.s3.ap-south-1.amazonaws.com/assets/allow_both.mp4",
      });
    } else if (!this.state.videoAccess && this.state.audioAccess) {
      this.setState({
        source:
          "https://birbal.s3.ap-south-1.amazonaws.com/assets/allow_cam.mp4",
      });
    } else if (this.state.videoAccess && !this.state.audioAccess) {
      this.setState({
        source:
          "https://birbal.s3.ap-south-1.amazonaws.com/assets/allow_mic.mp4",
      });
    }

    await this.getMicrophone();
  };

  componentDidUnmount() {
    window.onbeforeunload = null;
  }

  displaySpeed = () => {
    if (
      localStorage.getItem("uploadSpeed") &&
      localStorage.getItem("uploadSpeed") < 1 &&
      this.state.faces
    ) {
      this.props.UserAction.addEvent({
        responseId: this.state.responseId,
        event: {
          type: "interview",
          event: "Preview check failed",
          message: "Internet not stable",
          timestamp: new Date().toISOString(),
          internetSpeed:`${localStorage.getItem("uploadSpeed")}mbps`
        },
        isPreviewFail: true,
      });
      this.setState({
        showDialog: true,
      });
    }
  };

  async componentDidUpdate(prevProps) {
    const prev = prevProps && prevProps.UserReducer;
    const cur = this.props && this.props.UserReducer;
    // console.log('cur',cur)
    if (
      prev.isScreenShotDataArrived !== cur.isScreenShotDataArrived &&
      cur.screenShot &&
      cur.screenShot.data
    ) {
      cur.isScreenShotDataArrived = false;
      await this.setState({
        faces: cur.screenShot.data.faces ? cur.screenShot.data.faces : 0,
      });
      if (!cur.screenShot.data.faces) {
        this.props.UserAction.addEvent({
          responseId: this.state.responseId,
          event: {
            type: "interview",
            event: "Preview check failed",
            message: "No Face Detected",
            timestamp: new Date().toISOString(),
          },
          isPreviewFail: true,
        });
        store.addNotification({
          title: "",
          message:
            "Please Make Sure Your Face is visible and there is proper lighting!",
          type: "warning",
          container: "top-center",
          animationIn: ["animated", "fadeIn"],
          animationOut: ["animated", "fadeOut"],
          dismiss: {
            duration: 2500,
          },
        });
        setTimeout(() => {
          this.capture();
        }, 2500);
      } else {
        // let isSafari = /^((?!chrome|android).)*safari/i.test(
        //   navigator.userAgent
        // );
        var isChrome =
          /Chrome/.test(navigator.userAgent) &&
          /Google Inc/.test(navigator.vendor);
        if (!isChrome) {
          this.setState({
            showDialogScreen: true,
          });
        } else {
          this.childRef.current.getAccess();
        }
      }
    }

    if (cur.screenshotError) {
      cur.screenshotError = false;
      setTimeout(() => {
        this.capture();
      }, 5000);
    }
  }

  handleScreenAccess = () => {
    const access = this.childRef.current.getAccess();
    // console.log(access);

    this.setState({
      showDialogScreen: false,
    });
  };

  onScreenAccess = async () => {
    await this.setState({
      screenAccess: true,
    });

    this.displaySpeed();
  };

  capture = () => {
    const imgSrc =
      this.webcamRef &&
      this.webcamRef.current &&
      this.webcamRef.current.getScreenshot();
    let image = null;
    image =
      imgSrc && imgSrc.substring("data:image/jpeg;base64".length + 1)
        ? imgSrc.substring("data:image/jpeg;base64".length + 1)
        : "notfound";
    this.setState({
      screenShot: image,
    });

    this.props.UserAction.verifyScreenshot({
      base64: image,
    });
  };

  handleInterview = () => {
    let totalMcq = 0;
    let totalVerbal = 0;
    let totalCoding = 0;
    let sequenceOrder = "";
    let verbalQuestionsList = [];
    let mcqQuestionsList = [];
    let codingQuestionsList = [];
    const { response, questions, newResponse } =
      this.props &&
      this.props.history &&
      this.props.history.location &&
      this.props.history.location.state;
    console.log(response);

    const {
      questionType,
      mcqQuestions,
      verbalQuestions,
      codingQuestions,
      status,
    } = response;
    let order =
      questionType &&
      questionType.length > 0 &&
      questionType.map((el) => el.name && el.name.toLowerCase());
    // questionType[0] &&
    // questionType[0].name;

    mcqQuestions &&
      mcqQuestions.length > 0 &&
      mcqQuestions.map((el) => {
        if (el.statusCode === 1) {
          const question =
            questions &&
            questions.length > 0 &&
            questions.find((item) => el.id === item.id);
          if (question) {
            mcqQuestionsList.push(question);
          }
        }
      });
    codingQuestions &&
      codingQuestions.length > 0 &&
      codingQuestions.map((el) => {
        if (el.statusCode === 1) {
          const question =
            questions &&
            questions.length > 0 &&
            questions.find((item) => el.id === item.id);
          if (question) {
            codingQuestionsList.push(question);
          }
        }
      });
    verbalQuestions &&
      verbalQuestions.length > 0 &&
      verbalQuestions.find((el) => {
        if (el.statusCode === 1) {
          const question =
            questions &&
            questions.length > 0 &&
            questions.find((item) => el.id === item.id);
          if (question) {
            verbalQuestionsList.push(question);
          }
        }
      });
    totalMcq = mcqQuestionsList.length;
    totalVerbal = verbalQuestionsList.length;
    totalCoding = codingQuestionsList.length;
    if (totalMcq === 0 && order.includes("mcq")) {
      let index = order.indexOf("mcq");
      if (index !== -1) {
        order.splice(index, 1);
      }
    }
    if (totalVerbal === 0 && order.includes("verbal")) {
      let index = order.indexOf("verbal");
      if (index !== -1) {
        order.splice(index, 1);
      }
    }
    if (totalCoding === 0 && order.includes("coding")) {
      let index = order.indexOf("coding");
      if (index !== -1) {
        order.splice(index, 1);
      }
    }
    if (
      ((totalMcq > 0 && totalVerbal > 0 && totalCoding > 0) ||
        (totalMcq > 0 && totalVerbal > 0 && totalCoding === 0) ||
        (totalMcq > 0 && totalVerbal === 0 && totalCoding > 0) ||
        (totalMcq === 0 && totalVerbal > 0 && totalCoding > 0)) &&
      order &&
      order.length > 1
    ) {
      sequenceOrder = order && order.length > 0 && order[0];
      console.log(sequenceOrder, order);
    } else if (totalMcq > 0 && (totalVerbal === 0 || totalCoding === 0)) {
      sequenceOrder = "mcq";
    } else if (totalCoding > 0 && (totalVerbal === 0 || totalMcq === 0)) {
      sequenceOrder = "coding";
    } else if ((totalMcq === 0 || totalCoding === 0) && totalVerbal > 0) {
      sequenceOrder = "verbal";
    }
    if (totalMcq === 0 && totalVerbal === 0 && totalCoding === 0) {
      if (status === "APPEARING") {
        console.log(":hhhh");

        const payload = {
          responseId: this.state.responseId,
          formdata: { status: "FINISHED" },
        };
        this.props.UserAction.updateResponse(payload);
      }
      response &&
        this.state.responseId &&
        this.props.UserAction.addEvent({
          responseId: this.state.responseId,
          event: {
            type: "debug",
            level: 3,
            metadata: { Page: "Congratulation" },
            timestamp: new Date().toISOString(),
          },
        });

      this.props.history.push({
        pathname: "/congratulation",
      });
      localStorage.setItem("preview", true);
    } else {
      // console.log({
      //   data: { response, questions, newResponse },
      //   totalMcq,
      //   totalVerbal,
      //   totalCoding,
      //   sequenceOrder,
      //   verbalQuestions: verbalQuestionsList,
      //   mcqQuestions: mcqQuestionsList,
      //   codingQuestions: codingQuestionsList,
      //   order,
      // });
      navigator.mediaDevices
        .getUserMedia({ video: this.state.video, audio: this.state.audio })
        .then((stream) => {
          this.setState({ alert: false, enter: true });

          setTimeout(() => {
            response &&
              this.state.responseId &&
              this.props.UserAction.addEvent({
                responseId: this.state.responseId,
                event: {
                  type: "debug",
                  level: 3,
                  metadata: { Page: "Start Interview" },
                  timestamp: new Date().toISOString(),
                },
              });
            this.props.UserAction.addEvent({
              responseId: this.state.responseId,
              event: {
                type: "interview",
                event: "Preview check passed",
                timestamp: new Date().toISOString(),
              },
              isPreviewFail: false,
            });
            this.props.history.push({
              pathname: "/interview-process",
              state: {
                data: { response, questions, newResponse },
                totalMcq,
                totalVerbal,
                totalCoding,
                sequenceOrder,
                verbalQuestions: verbalQuestionsList,
                mcqQuestions: mcqQuestionsList,
                codingQuestions: codingQuestionsList,
                order,
              },
            });
          }, 1000);
          localStorage.setItem("preview", true);
        })

        .catch(() => {
          this.setState({ alert: true });
        });
    }
  };

  async getMicrophone() {
    const audio = await navigator.mediaDevices.getUserMedia({
      audio: true,
    });
    await this.setState({ audioNew: audio });
    this.setState({
      audioPopup: true,
    });

    setTimeout(() => {
      if (!amplitude) {
        this.setState({
          voiceMessage:
            "Please check your microphone, not able to listen to your voice",
        });
      }
    }, 2000);
  }

  stopMicrophone() {
    this.state.audioNew.getTracks().forEach((track) => track.stop());
    this.setState({ audioNew: null });
  }

  toggleMicrophone = () => {
    if (this.state.audioNew) {
      this.stopMicrophone();
    } else {
      this.getMicrophone();
    }
  };
  onUserStop = () => {
    this.setState({
      screenAccess: false,
      showDialog: false,
    });
  };

  amplitudeEvent = (level) => {
    // console.log("level", level);
    amplitude = level;

    if (
      (level === 1 || level === 2) &&
      this.state.voiceMessage !==
        "Please check your microphone, you voice is very low"
    ) {
      this.setState({
        voiceMessage: "Please check your microphone, you voice is very low",
      });
    }
    if (level === 3 || level === 4) {
      this.handleCloseAudioPopup();
    }
  };

  handleCloseAudioPopup = () => {
    navigator.mediaDevices.getUserMedia({audio: true})
  .then(stream => {
    console.log("MediaStreamTrack muted:", stream.getAudioTracks()[0].muted);

    if(!stream.getAudioTracks()[0].muted){
      this.setState({
        audioPopup: false,
      });
  
      this.capture();
    }
  })
  .catch(err => console.log(err));

  };

  render() {
    const videoConstraints = {
      facingMode: "user",
    };

    return localStorage.getItem("login") ? (
      <div className='preview-main-div'>
        <ScreenRecording
          ref={this.childRef}
          onScreenAccess={this.onScreenAccess}
          onUserStop={this.onUserStop}
        />
        <Dialog
          className='dialog-box'
          open={this.state.showDialog}
          onClose={() => {
            this.setState({
              showDialog: false,
            });
          }}
          aria-labelledby='alert-dialog-title'
          aria-describedby='alert-dialog-description'
        >
          <div className='dialog-wrapper'>
            <div className='left-div'>
              <div className='unstable-image'>
                <img src={warning} />
              </div>
              <div className='unstable-text-1'>
                Your internet connection is unstable
              </div>
              <div className='unstable-text-2'>
                Something is temporarily wrong with your internet connection
              </div>
            </div>

            <div className='right-div'>
              <div onClick={this.handleInterview} className='Modal-div'>
                Continue Anyway <ArrowRightIcon style={{ color: "#fff" }} />
              </div>
            </div>
          </div>
        </Dialog>

        <Dialog
          className='dialog-box'
          open={this.state.showDialogScreen}
          onClose={() => {
            this.setState({
              showDialogScreen: false,
            });
          }}
          aria-labelledby='alert-dialog-title'
          aria-describedby='alert-dialog-description'
        >
          <div className='dialog-wrapper'>
            <div className='left-div'>
              <div className='unstable-text-1'>
                Please Click on button to provide access
              </div>
            </div>

            <div className='right-div'>
              <div onClick={this.handleScreenAccess} className='Modal-div'>
                Screen Access
              </div>
            </div>
          </div>
        </Dialog>
        {this.state.isShowModal ? (
          <Modal
            isShowModal={this.state.isShowModal}
            onCloseModal2={() => {
              this.setState({
                isShowModal: false,
              });
            }}
            showClose={true}
            className='FirstModal'
          >
            <div className='FirstModal-main'>
              {window.screen.width >= 1024 ? (
                <video controls autoPlay={true}>
                  {this.state.source ? (
                    <source src={this.state.source} type='video/mp4' />
                  ) : null}
                </video>
              ) : null}

              <div className='message'>
                Please Allow
                {!this.state.videoAccess && !this.state.audioAccess
                  ? " Microphone and Camera/Webcam "
                  : !this.state.videoAccess && this.state.audioAccess
                  ? " Camera/Webcam "
                  : this.state.videoAccess && !this.state.audioAccess
                  ? " Microphone "
                  : null}
                Settings
              </div>
            </div>
          </Modal>
        ) : null}
        {this.state.audioPopup ? (
          <Modal
            isShowModal={this.state.audioPopup}
            // onCloseModal2={() => {
            //   this.setState({
            //     isShowModal: false,
            //   });
            // }}
            showClose={false}
            className='SecondModal'
          >
            <div className='modal-body'>
              <div className='mic-icon-div'>
                <MicIcon />
              </div>

              <div className='audio-anayliser-div'>
              {this.state.audioNew && this.state.audioAccess?   <AudioAnalyser
                  amplitude={this.amplitudeEvent}
                  audio={this.state.audioNew}
                /> : null}
              </div>
              <div className='speak'>Please Speak Your Name</div>
              <div className='button-msg-wrapper'>
                {amplitude === 1 || amplitude === 2 ? (
                  <div
                    className='Modal-div'
                    onClick={() => {
                      this.handleCloseAudioPopup();
                    }}
                  >
                    Continue Anyway <ArrowRightIcon style={{ color: "#fff" }} />
                  </div>
                ) : null}

                <div className='voice-message'>{this.state.voiceMessage}</div>
              </div>
            </div>
          </Modal>
        ) : null}
        <div className='previews-heading'>
          {window.screen.width > 1024 ? <Typography>Preview</Typography> : null}
        </div>
        <div className='previews-subHeading1'>
          {window.screen.width > 1024 ? (
            <Typography>Check all connections before starting</Typography>
          ) : null}
        </div>

        <div className='page-content-preview '>
          <div className='row-wrapper'>
            <div className='left-div-new'>
              <div className='video-div-preview'>
                <div className='interview-video'>
                  <Webcam
                    muted={true}
                    audio={this.state.audio}
                    ref={this.webcamRef}
                    videoConstraints={videoConstraints}
                    autoPlay
                    screenshotFormat='image/jpeg'
                  />
                </div>
              </div>
              <div className='second-wrapper'>
                <div className='birbal-logo-preview2'>
                  <img style={{ maxWidth: "100px" }} src={birballogo}></img>
                </div>
                <div className='turn-on-2'>
                  Please turn on the Video and mic before starting the
                  interview.
                </div>

                <div className='main-flex'>
                  <div style={{ display: "flex" }} className='tick-div'>
                    <div className='flex-div-preview'>
                      {this.state.audioAccess ? (
                        <div
                          style={{ backgroundColor: "#04724D" }}
                          className='icon-box'
                        >
                          <CheckIcon
                            style={{
                              color: "#fff",
                              font: "bold",
                              height: "15px",
                              width: "15px",
                            }}
                          />
                        </div>
                      ) : (
                        <div
                          style={{ backgroundColor: "#F11A1A" }}
                          className='icon-box'
                        >
                          {" "}
                          <ClearIcon
                            style={{
                              color: "#fff",
                              font: "bold",
                              height: "15px",
                              width: "15px",
                            }}
                          />
                        </div>
                      )}{" "}
                      <div className='access-text'> Audio Access</div>
                    </div>

                    <div className='flex-div-preview'>
                      {this.state.videoAccess ? (
                        <div
                          style={{ backgroundColor: "#04724D" }}
                          className='icon-box'
                        >
                          <CheckIcon
                            style={{
                              color: "#fff",
                              font: "bold",
                              height: "15px",
                              width: "15px",
                            }}
                          />
                        </div>
                      ) : (
                        <div
                          style={{ backgroundColor: "#F11A1A" }}
                          className='icon-box'
                        >
                          {" "}
                          <ClearIcon
                            style={{
                              color: "#fff",
                              font: "bold",
                              height: "15px",
                              width: "15px",
                            }}
                          />
                        </div>
                      )}{" "}
                      <div className='access-text'>Video Access</div>
                    </div>

                    <div className='only-tab-div flex-div-preview'>
                      {this.state.faces ? (
                        <div
                          style={{ backgroundColor: "#04724D" }}
                          className='icon-box'
                        >
                          <CheckIcon
                            style={{
                              color: "#fff",
                              font: "bold",
                              height: "15px",
                              width: "15px",
                            }}
                          />
                        </div>
                      ) : (
                        <div
                          style={{ backgroundColor: "#F11A1A" }}
                          className='icon-box'
                        >
                          {" "}
                          <ClearIcon
                            style={{
                              color: "#fff",
                              font: "bold",
                              height: "15px",
                              width: "15px",
                            }}
                          />
                        </div>
                      )}{" "}
                      <div className='access-text'>Lighting Conditions</div>
                    </div>

                    <div className='only-tab-div flex-div-preview'>
                      {localStorage.getItem("uploadSpeed") &&
                      localStorage.getItem("uploadSpeed") > 1 ? (
                        <div
                          style={{ backgroundColor: "#04724D" }}
                          className='icon-box'
                        >
                          <CheckIcon
                            style={{
                              color: "#fff",
                              font: "bold",
                              height: "15px",
                              width: "15px",
                            }}
                          />
                        </div>
                      ) : (
                        <div className='icon-box'>
                          <InfoIcon
                            style={{
                              font: "bold",
                              height: "100%",
                              width: "100%",
                            }}
                          />{" "}
                        </div>
                      )}{" "}
                      <div className='access-text'>Internet Speed</div>
                    </div>

                    <div className='only-tab-div flex-div-preview'>
                      {this.state.screenAccess ? (
                        <div
                          style={{ backgroundColor: "#04724D" }}
                          className='icon-box'
                        >
                          <CheckIcon
                            style={{
                              color: "#fff",
                              font: "bold",
                              height: "15px",
                              width: "15px",
                            }}
                          />
                        </div>
                      ) : (
                        <div
                          style={{ backgroundColor: "#F11A1A" }}
                          className='icon-box'
                        >
                          {" "}
                          <ClearIcon
                            style={{
                              color: "#fff",
                              font: "bold",
                              height: "15px",
                              width: "15px",
                            }}
                          />
                        </div>
                      )}{" "}
                      <div className='access-text'>Screen Access</div>
                    </div>
                  </div>

                  {this.state.audioNew && this.state.audioAccess ? (
                    <div className='audio-bar'>
                      <div className='flex-div-preview-2'>
                        <div className='div-1'>
                          {" "}
                          <img src={audio} />{" "}
                        </div>
                        <div style={{ height: "20px" }} className='div-2'>
                          {" "}
                          <AudioAnalyser
                            amplitude={() => null}
                            audio={this.state.audioNew}
                          />{" "}
                        </div>
                      </div>
                    </div>
                  ) : null}
                </div>

                <div className='main-flex-new'>
                  <div style={{ display: "flex" }} className='tick-div-new'>
                    <div className='flex-div-preview'>
                      {this.state.faces ? (
                        <div
                          style={{ backgroundColor: "#04724D" }}
                          className='icon-box'
                        >
                          <CheckIcon
                            style={{
                              color: "#fff",
                              font: "bold",
                              height: "15px",
                              width: "15px",
                            }}
                          />
                        </div>
                      ) : (
                        <div
                          style={{ backgroundColor: "#F11A1A" }}
                          className='icon-box'
                        >
                          {" "}
                          <ClearIcon
                            style={{
                              color: "#fff",
                              font: "bold",
                              height: "15px",
                              width: "15px",
                            }}
                          />
                        </div>
                      )}{" "}
                      <div className='access-text'>Lighting Conditions</div>
                    </div>

                    <div className='flex-div-preview'>
                      {localStorage.getItem("uploadSpeed") &&
                      localStorage.getItem("uploadSpeed") > 1 ? (
                        <div
                          style={{ backgroundColor: "#04724D" }}
                          className='icon-box'
                        >
                          <CheckIcon
                            style={{
                              color: "#fff",
                              font: "bold",
                              height: "15px",
                              width: "15px",
                            }}
                          />
                        </div>
                      ) : (
                        <div className='icon-box'>
                          <InfoIcon
                            style={{
                              font: "bold",
                              height: "100%",
                              width: "100%",
                            }}
                          />{" "}
                        </div>
                      )}{" "}
                      <div className='access-text'>Internet Speed</div>
                    </div>
                    <div className='flex-div-preview'>
                      {this.state.screenAccess ? (
                        <div
                          style={{ backgroundColor: "#04724D" }}
                          className='icon-box'
                        >
                          <CheckIcon
                            style={{
                              color: "#fff",
                              font: "bold",
                              height: "15px",
                              width: "15px",
                            }}
                          />
                        </div>
                      ) : (
                        <div
                          style={{ backgroundColor: "#F11A1A" }}
                          className='icon-box'
                        >
                          {" "}
                          <ClearIcon
                            style={{
                              color: "#fff",
                              font: "bold",
                              height: "15px",
                              width: "15px",
                            }}
                          />
                        </div>
                      )}{" "}
                      <div className='access-text'>Screen Access</div>
                    </div>
                  </div>
                </div>
                {this.state.audioNew && this.state.audioAccess ? (
                  <div className='audio-bar-2'>
                    <div className='flex-div-preview-2'>
                      <div className='div-1'>
                        {" "}
                        <img src={audio} />{" "}
                      </div>
                      <div style={{ height: "20px" }} className='div-2'>
                        {" "}
                        <AudioAnalyser
                          amplitude={() => null}
                          audio={this.state.audioNew}
                        />{" "}
                      </div>
                    </div>
                  </div>
                ) : null}

                <div className='only-mobile-button'>
                  {this.state.videoAccess &&
                  this.state.audioAccess &&
                  this.state.screenAccess &&
                  this.state.faces ? (
                    <Button
                      className='startInterviewButton'
                      variant='contained'
                      onClick={this.handleInterview}
                      style={{
                        marginTop: "30px",
                        backgroundColor: "#04724D",
                        color: "white",
                        borderRadius: "4px",
                        textTransform: "none",
                        fontSize: "13px",
                        fontWeight: "500",
                        whiteSpace: "nowrap",
                        width: "100%",
                      }}
                    >
                      Start Interview
                    </Button>
                  ) : (
                    <div style={{ padding: "20px 0" }}>
                      <CircularProgress style={{ color: "#04724D" }} />
                    </div>
                  )}
                </div>
              </div>
            </div>
            <div className='preview-start previews-subHeading right-div-new'>
              <Typography>
                Please turn on the Video and mic before starting the interview.
              </Typography>
              {this.state.videoAccess &&
              this.state.audioAccess &&
              this.state.faces &&
              this.state.screenAccess ? (
                <Button
                  className='startInterviewButton'
                  variant='contained'
                  onClick={this.handleInterview}
                  style={{
                    marginTop: "30px",
                    backgroundColor: "#04724D",
                    color: "white",
                    borderRadius: "20px",
                    padding: "12px 60px",
                    textTransform: "none",
                    fontSize: "22px",
                    fontWeight: "600",
                    whiteSpace: "nowrap",
                  }}
                >
                  Start Interview
                </Button>
              ) : (
                <div style={{ padding: "20px 0" }}>
                  <CircularProgress style={{ color: "#04724D" }} />
                </div>
              )}
            </div>
          </div>
        </div>
        <div
          style={{
            display: "flex",
            justifyContent: "end",
            marginRight: "30px",
          }}
          className='birbal-logo-preview'
        >
          <img style={{ maxWidth: "100px" }} src={birballogo}></img>
        </div>
      </div>
    ) : (
      <div> Access Blocked</div>
    );
  }
}
const mapStateToProps = (state) => ({
  UserReducer: state.UserReducer,
});

function mapDispatchToProps(dispatch) {
  return {
    UserAction: bindActionCreators(Actions, dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Preview));
